define('uhuraapp/components/share-modal', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    currentURL: Ember['default'].computed(function () {
      return window.location.toString().split('?')[0] + '?utm_source=uhura+modal&utm_medium=site';
    }),

    actions: {
      close: function close() {
        this.sendAction(this.attrs.dismiss);
      }
    }
  });

});