define('uhuraapp/mixins/routes/material-design', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Mixin.create({
    setupController: function setupController(a, b) {
      this._super(a, b);
      Ember['default'].run.scheduleOnce('afterRender', this, 'upgradeElements');
    },
    upgradeElements: function upgradeElements() {
      window.componentHandler.upgradeDom('MaterialButton');
      window.componentHandler.upgradeDom('MaterialLayout');
    }
  });

});