define('uhuraapp/models/episode', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

  'use strict';

  /* global moment */
  var computed = Ember['default'].computed;

  exports['default'] = DS['default'].Model.extend({
    title: DS['default'].attr('string'),
    description: DS['default'].attr('string'),
    source_url: DS['default'].attr('string'),
    channel_id: DS['default'].attr(),
    channel: DS['default'].belongsTo('channel'),
    listened: DS['default'].attr(),
    played: computed('listened', function () {
      return this.get('listened');
    }),
    published_at: DS['default'].attr('date'),
    stopped_at: DS['default'].attr('number'),
    source: computed('source_url', function () {
      return this.get('source_url');
    }),
    publishedAt: computed('published_at', function () {
      return moment(this.get('published_at')).format('MMM Do YY');
    }),
    init: function init() {
      this._super();
      this.set('progress', 0);
    }
  });

});