define('uhuraapp/tests/unit/adapters/application-test', ['ember-qunit'], function (ember_qunit) {

  'use strict';

  ember_qunit.moduleFor('adapter:application', 'ApplicationAdapter', {
    // needs: ['serializer:foo']
  });

  ember_qunit.test('it exists', function (assert) {
    var adapter = this.subject();
    assert.ok(adapter);
  });

  ember_qunit.test('it has v2 namespace', function (assert) {
    assert.equal(this.subject().namespace, 'v2');
  });

  ember_qunit.test('it has host', function (assert) {
    assert.equal(this.subject().host, 'http://api.uhura.io');
  });

  ember_qunit.test('pathForType fix users path', function (assert) {
    var _this = this;

    var path = function path(v) {
      return _this.subject().pathForType(v);
    };
    assert.equal(path('test'), 'tests');
    assert.equal(path('subscription'), 'users/subscriptions');
  });

});