define('uhuraapp/mixins/routes/authentication', ['exports', 'ember', 'uhuraapp/mixins/routes/material-design'], function (exports, Ember, MaterialDesignMixin) {

  'use strict';

  exports['default'] = Ember['default'].Mixin.create(MaterialDesignMixin['default'], {
    session: Ember['default'].inject.service('session'),
    beforeModel: function beforeModel(transition) {
      if (this.get('session').get('isAuthenticated')) {
        transition.abort();
        this.transitionTo('index');
      }
      return this._super(transition);
    }
  });

});