define('uhuraapp/components/episodes-list', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  /* global moment */
  exports['default'] = Ember['default'].Component.extend({
    classNames: ['episodes'],
    tagName: 'ul',
    hasMore: true,
    page: 1,
    infiniteScroll: true,
    hasRightActions: true,
    actions: {
      fetchMore: function fetchMore(callback) {
        var promise = Ember['default'].RSVP.Promise.reject();

        if (this.get('infiniteScroll')) {
          var episodes = this.get('episodes');
          var lastEpisode = episodes.get('lastObject');
          var date = lastEpisode ? lastEpisode.get('published_at') : 1999;
          var since = moment.utc(date).format();

          // TODO: use meta
          promise = this.get('store').query('episode', {
            since: since,
            channel_id: this.get('channel.raw_id'),
            per_page: 20
          }).then(function (response) {
            // Fix error with _internalModel
            var items = [];
            response.get('content').forEach(function (internalModel) {
              items.push({ _internalModel: internalModel });
            });
            response.set('content', items);
            return response;
          });
        }

        callback(promise);
      }
    }
  });

});