define('uhuraapp/authorizers/uhura', ['exports', 'ember', 'ember-simple-auth/authorizers/base'], function (exports, Ember, Base) {

  'use strict';

  var isEmpty = Ember['default'].isEmpty;

  exports['default'] = Base['default'].extend({
    session: Ember['default'].inject.service('session'),
    authorize: function authorize(block) {
      var token = this.get('session.authenticated.token');
      if (this.get('session.isAuthenticated') && !isEmpty(token)) {
        block('Authorization', 'Token ' + token);
      }
    }
  });

});