define('uhuraapp/app', ['exports', 'ember', 'ember/resolver', 'ember/load-initializers', 'uhuraapp/config/environment', 'uhuraapp/utils/route'], function (exports, Ember, Resolver, loadInitializers, config, routeUtil) {

  'use strict';

  Ember['default'].MODEL_FACTORY_INJECTIONS = true;

  var App = Ember['default'].Application.extend({
    modulePrefix: config['default'].modulePrefix,
    podModulePrefix: config['default'].podModulePrefix,
    Resolver: Resolver['default']
  });

  routeUtil['default']();

  loadInitializers['default'](App, config['default'].modulePrefix);

  var image = new Image();
  image.src = '/assets/channel-placeholder.png';

  exports['default'] = App;

});