define('uhuraapp/transitions', ['exports'], function (exports) {

  'use strict';

  var duration = 450;

  exports['default'] = function () {
    this.transition(this.fromRoute('subscriptions'), this.toRoute('channel'), this.use('explode', {
      matchBy: 'data-channel-image-id',
      use: ['flyTo', { duration: duration }]
    }));

    this.transition(this.fromRoute('channel'), this.toRoute('subscriptions'), this.use('explode', {
      matchBy: 'data-channel-image-id',
      use: ['flyTo', { duration: duration }]
    }));

    this.transition(this.hasClass('share-dialog'), this.toValue(true), this.use('toUp'), this.reverse('toUp', { duration: duration }));
  }

});