define('uhuraapp/mixins/models/channel', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

  'use strict';

  var computed = Ember['default'].computed;

  var IMAGE_HOST = 'http://arcane-forest-5063.herokuapp.com';

  exports['default'] = Ember['default'].Mixin.create({
    title: DS['default'].attr('string'),
    image_url: DS['default'].attr('string'),
    description: DS['default'].attr('string'),
    copyrigth: DS['default'].attr('string'),
    episodes: DS['default'].hasMany('episode', { async: false }),
    raw_id: DS['default'].attr('string'),
    subscribed: DS['default'].attr(),
    to_view: DS['default'].attr(),
    url: DS['default'].attr(),
    uri: DS['default'].attr(),
    channel_id: DS['default'].attr(),
    channel_url: DS['default'].attr(),
    imageURL: computed('image_url', function () {
      var that = this;
      var image = new Image();
      image.src = this._realImageUrl();
      image.onload = function () {
        var _this = this;

        if (typeof this.naturalWidth !== 'undefined' && this.naturalWidth !== 0) {
          Ember['default'].run(function () {
            if (!that.isDestroyed) {
              that.set('imageURL', _this.src);
            }
          });
        }
      };

      return '/assets/channel-placeholder.png';
    }),

    _realImageUrl: function _realImageUrl() {
      var url = this.get('image_url');
      if (url !== '' && url.indexOf(IMAGE_HOST) === -1) {
        return IMAGE_HOST + '/resolve?url=' + this.get('image_url');
      }
      return url;
    }
  });

});