define('uhuraapp/components/the-player', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    player: Ember['default'].inject.service('player'),
    classNames: ['player-wrapper'],
    classNameBindings: ['hasModel'],
    hasModel: Ember['default'].computed.bool('controller.player.current'),

    episode: Ember['default'].computed.alias('player.current'),

    actions: {
      playpause: function playpause() {
        var player = this.get('player');
        var episode = this.get('episode');
        player.playpause(episode);
      }
    }
  });

});