define('uhuraapp/controllers/login', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var service = Ember['default'].inject.service;

  exports['default'] = Ember['default'].Controller.extend({
    processing: false,
    processingMessage: '',

    session: service('session'),

    actions: {
      authenticate: function authenticate(provider) {
        var _this = this;

        this.set('processing', true);
        this.set('errorMessage', false);

        var email = this.get('email');
        var password = this.get('password');
        var data = { email: email, password: password, provider: provider };

        if (provider) {
          this.set('processingMessage', provider + ' authorization');
        }

        this.get('session').authenticate('authenticator:uhura', data).then(function () {
          _this.set('loading', false);
          _this.set('processing', false);
          _this.set('processingMessage', '');
        })['catch'](function (errorMessage) {
          _this.set('errorMessage', errorMessage);
          _this.set('processing', false);
          _this.set('processingMessage', '');
        });
      }
    }
  });

});