define('uhuraapp/routes/channel', ['exports', 'ember', 'uhuraapp/mixins/routes/material-design'], function (exports, Ember, MaterialDesignMixin) {

  'use strict';

  var service = Ember['default'].inject.service;

  exports['default'] = Ember['default'].Route.extend(MaterialDesignMixin['default'], {
    session: service('session'),
    beforeModel: function beforeModel(transition) {
      this.get('session').set('attemptedTransition', transition);
      return this._super(transition);
    },
    model: function model(params) {
      return this.store.find('channel', params.channel_id);
    }
  });

});