define('uhuraapp/adapters/application', ['exports', 'ember-data', 'uhuraapp/config/environment', 'ember-simple-auth/mixins/data-adapter-mixin'], function (exports, DS, ENV, DataAdapterMixin) {

  'use strict';

  exports['default'] = DS['default'].RESTAdapter.extend(DataAdapterMixin['default'], {
    authorizer: 'authorizer:uhura',
    namespace: 'v2',
    host: ENV['default'].API_URL,
    pathForType: function pathForType(type) {
      var prefix = this.__isUserType(type) ? 'users/' : '';
      return prefix + this._super(type);
    },
    __isUserType: function __isUserType(type) {
      return ['subscription'].indexOf(type) > -1;
    }
  });

});