define('uhuraapp/components/episode-list-item', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    tagName: 'li',
    classNames: ['episode'],
    classNameBindings: ['isPlayed', 'isDownloaded', 'isPlaying'],
    rightActions: true,

    player: Ember['default'].inject.service('player'),
    client: Ember['default'].inject.service('uhura-client'),

    isPlayed: Ember['default'].computed.bool('episode.played'),
    isDownloaded: Ember['default'].computed.bool('episode.downloaded'),
    isPlaying: Ember['default'].computed.bool('episode.playing'),

    actions: {
      playpause: function playpause() {
        this.get('player').playpause(this.get('episode'));
      },

      played: function played() {
        var episode = this.get('episode');
        var isPlayed = !!episode.get('played');
        var method = isPlayed ? 'DELETE' : 'POST';

        episode.set('played', !isPlayed); // early visual response

        this.get('client').request('episode', episode.id, 'played', method)['catch'](function () {
          episode.set('played', isPlayed); // rollback
        });

        Ember['default'].$('.more-itens .itens.open').removeClass('.open');
      },

      more: function more() {
        Ember['default'].$('.itens.open').removeClass('open');
        this.$('.itens').addClass('open');
        Ember['default'].run.later(function () {
          Ember['default'].$(document).on('click.out-itens', 'body', function (e) {
            Ember['default'].$('.itens.open').removeClass('open');
            Ember['default'].$(document).off('click.out-itens');
            e.stopPropagation();
          });
        }, 500);
      },

      info: function info() {
        Ember['default'].$('.body.open').removeClass('open');
        this.$('.body').addClass('open');
      }
    }
  });

});