define('uhuraapp/services/uhura-client', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Service.extend({
    adapter: function adapter() {
      return this.container.lookup('adapter:application');
    },

    request: function request(modelName, id, action, type, options) {
      var url = this.adapter().buildURL(modelName, id) + '/' + action;
      return this.adapter().ajax(url, type, options);
    }
  });

});