define('uhuraapp/tests/unit/routes/application-test', ['ember-qunit', 'ember'], function (ember_qunit, Ember) {

  'use strict';

  var Evented = Ember['default'].Evented;

  var session = Ember['default'].Object.extend(Evented, {
    content: {}
  }).create();

  var sessionService = Ember['default'].Object.extend(Evented);

  ember_qunit.moduleFor('route:application', {
    beforeEach: function beforeEach() {
      var container = this.container;

      var service = sessionService.create({ container: container, session: session });
      this.registry.register('service:session', service, { instantiate: false });
    }
  });

  ember_qunit.test('it exists', function (assert) {
    var route = this.subject();
    assert.ok(route);
  });

});